import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {AngularFirestore} from "@angular/fire/firestore";
import {Observable} from "rxjs";
import {Collections, Content} from "../../app/app.models";

@IonicPage()
@Component({
  selector: 'page-programa-bloque',
  templateUrl: 'programa-bloque.html',
})
export class ProgramaBloquePage {

  public bloque$: Observable<Content>;
  public temas$: Observable<Content[]>;

  private id: string = this.navParams.get('id');

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private afs: AngularFirestore
  ) {
  }

  ionViewDidLoad() {
    this.bloque$ = this.afs.collection(Collections.CONTENT).doc<Content>(this.id).valueChanges();
    this.temas$ = this.afs.collection<Content>(Collections.CONTENT, ref => ref.where('parent_id', '==', this.id)).valueChanges();
  }

  openTema(id: string) {
    this.navCtrl.push('ClaseDetailPage', {type: 'tema', id});
  }

}



